@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;1,300;1,400&display=swap);
:root{--primary-font: 'Titillium Web', sans-serif;--text-black: #171717;--white: #ffffff}*{font-family:'Titillium Web', sans-serif;font-family:var(--primary-font)}html{color:#171717;color:var(--text-black)}

.Header{grid-column:1/-1;display:flex;justify-content:space-between;align-items:center;padding:0 16px;box-shadow:0px 15px 8px 0px #171717;position:relative}.Header:after{content:"";box-shadow:0px 12px 8px 0px #171717;width:100%;height:30px;left:0;right:0;top:100%;position:absolute}.Header__headline-wrap{font-size:24pt;font-weight:600;color:var(--white);display:flex;margin:0}.Header__back-btn,.Header__search-btn{background:transparent;outline:none;box-shadow:none;border:none}.Header__back-btn{margin-right:12px}.Header__back-btn-icon{height:24px}.Header__search-btn-icon{height:24px}

.App{background-color:#171717;height:calc(100vh - 30px);padding:30px 0 0;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:auto 1fr;grid-gap:36px 30px}.App__loading{grid-column:1/-1;text-align:center}

